.joke-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 1em;
}

.joke {
  margin-right: 1em;
  margin-bottom: 1em;
  flex-basis: 300px;
  flex-grow: 1;
  color: var(--main-grey);
  padding: 1em;
  transition: box-shadow 100ms;
  border: 1px solid var(--white-three);
  background-color: var(--white-two);

  &:hover {
    box-shadow: 0 1px 7px 0 rgba($color: #000000, $alpha: 0.15);
  }

  &__title {
    margin-bottom: 1em;
    font-weight: 700;
  }

  &__bolt-icon {
    color: var(--category-8);
    margin-right: 0.5em;
    transform: translate(0);
    display: inline-block;
  }

  &__content {
    margin-bottom: 2em;
  }

  &__button-wrapper {
    text-align: right;
  }
}

.pagination-buttons {
  margin: 0 auto;
  margin-top: 2em;
  list-style: none;
  display: flex;
  justify-content: center;
  color: #737375;

  li {
    background-color: #fff;
  }
  a {
    display: block;
    padding: 0.7em 1em;
  }
  a:hover {
    color: white;
    background-color: #6c7889;
    cursor: pointer;
  }
  .pagination-active a {
    color: white;
    background-color: #494949;
  }
}
